import { Validator } from 'vee-validate';
const dictionary = {
    en: {
        messages:{
            numeric: () => 'Some English Message'
        }
    },
    cn: {
        messages: {
            numeric(){
                return '电话号码只能是数字';
            }
        }
    }
};

Validator.updateDictionary(dictionary);
const validator = new Validator({ first_name: 'numeric' });

validator.setLocale('cn'); // now this validator will generate messages in arabic.


